.exchangesContainer
    padding: 0 200px
    overflow: hidden
    position: relative
    &::before
        content: ""
        background-image: url("../../Images/BackGroundHero.png")
        background-position: center
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        opacity: 0.03
        z-index: -5
    .exchangesFeaturedHeader
        margin-top: 120px
        font-size: 3rem
        color: white
        font-weight: 600px
        line-height: 4rem

    .exchangesMidLine
        position: relative
        padding: 60px 0px
        background: radial-gradient(rgba(85, 113, 254, 0.020), rgba(85, 113, 254, 0.015), rgba(85, 113, 254, 0.010) , rgba(85, 113, 254, 0.005), rgba(85, 113, 254, 0) 72%)
    .exchangesMidLine::before
        content: ''
        position: absolute
        left: 0
        right: 0
        height: 3px
        background: linear-gradient(to right, transparent, #1E1F25, transparent)
    .exchangesTable
        .exchangesTableHeader
            display: grid
            grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr
            .exchangesTableParameters, .exchangesTableParametersSort
                display: flex
                justify-content: center
                align-items: center
                .exchangesTableParametersText
                    color: #808A9A
                .exchangesTableParametersImg
                    height: 10px
                    width: 10px
                    margin-left: 5px
            .exchangesTableParametersSort:hover
                cursor: pointer

    .exchangeTableContainer
        background: radial-gradient(rgba(85, 113, 254, 0.082), rgba(85, 113, 254, 0.062), rgba(85, 113, 254, 0.048) , rgba(85, 113, 254, 0.032), rgba(85, 113, 254, 0) 72%)

@media (min-width: 1441px) and (max-width: 1550px)
    .exchangesContainer
        padding: 0 200px
        .exchangesContainer::before
        .exchangesFeaturedHeader
        .exchangesMidLine
            .exchangesMidLine::before
        .exchangesTable
            .exchangesTableHeader
                .exchangesTableParameters
                .exchangesTableParametersSort
                    .exchangesTableParametersText
                    .exchangesTableParametersImg
        .exchangeTableContainer
@media (min-width: 1101px) and (max-width: 1440px)
    .exchangesContainer
        padding: 0 100px
        .exchangesContainer::before
        .exchangesFeaturedHeader
            margin-top: 90px
            font-size: 2.4rem
            line-height: 3.2rem
        .exchangesMidLine
            .exchangesMidLine::before
        .exchangesTable
            .exchangesTableHeader
                .exchangesTableParameters
                .exchangesTableParametersSort
                    .exchangesTableParametersText
                    .exchangesTableParametersImg
        .exchangeTableContainer
