        .exchangesFeaturedHeader
            margin-top: 60px
            font-size: 2.2rem
            line-height: 2.6rem
        .exchangesMidLine
            padding: 30px 0px
            .exchangesMidLine::before
        .exchangesTable
            .exchangesTableHeader
                .exchangesTableParameters
                .exchangesTableParametersSort
                    .exchangesTableParametersText
                    .exchangesTableParametersImg
        .exchangeTableContainer
@media (min-width: 511px) and (max-width: 690px)
    .exchangesContainer
        padding: 0 50px
        .exchangesContainer::before
        .exchangesFeaturedHeader
            font-size: 2rem
            line-height: 2.6rem
            width: 300px
            margin: 0 auto
            margin-top: 30px
        .exchangesMidLine
            padding: 30px 0px
            .exchangesMidLine::before
        .exchangesTable
            .exchangesTableHeader
                .exchangesTableParameters
                .exchangesTableParametersSort
                    .exchangesTableParametersText
                    .exchangesTableParametersImg
        .exchangeTableContainer
@media (max-width: 510px)
