.questionsContainer {
  padding: 50px 200px;
  display: flex;
  flex-direction: column;
  position: relative;
  background: radial-gradient(rgba(85, 113, 254, 0.082), rgba(85, 113, 254, 0.062), rgba(85, 113, 254, 0.048), rgba(85, 113, 254, 0.032), rgba(85, 113, 254, 0) 58%);
}
.questionsContainer::before {
  content: "";
  background-image: url("../../Images/BackGroundHero.png");
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.08;
  z-index: -1;
}
.questionsContainer .questionsContainerTop .questionsHeader {
  color: #F7FAFF;
  font-size: 3rem;
  line-height: 4rem;
  margin: 0;
}
.questionsContainer .questionsContainerTop .questionsHeader span {
  color: #1B70F1;
}
.questionsContainer .questionsContainerTop .questionsHeaderDescription {
  margin-top: 30px;
  font-size: 1.2rem;
  color: #ADB3BF;
  line-height: 2rem;
}
.questionsContainer .questionsMidLine {
  position: relative;
  padding: 60px 0px;
}
.questionsContainer .questionsMidLine::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(to right, transparent, #1E1F25, transparent);
}
.questionsContainer .questionsContainerBottom {
  display: flex;
  flex-direction: row;
  position: relative;
  height: 430px;
}
.questionsContainer .questionsContainerBottom .questionsContainerLeft {
  position: absolute;
  left: 50px;
  width: 50%;
}
.questionsContainer .questionsContainerBottom .questionsContainerRight {
  position: absolute;
  right: 50px;
  width: 50%;
}

